<template>
  <div class="flex flex-wrap mx-auto p-3 sm:p-8">
    <div class="w-1/2 sm:w-1/5 px-3 py-3" v-for="(item, index) in list_portofolio" :key="index">
      <div class="rounded overflow-hidden shadow hover:shadow-lg border-box">
        <img class="w-full" :src="vue_logo" :alt="item.name">
        <div class="text-sm sm:text-md mt-3 pr-3 pl-3">Project Name : {{ item.name }}</div>
        <div class="px-3">
          <a :href="`${base_url}${item.name}`">
            <button
              class="w-full bg-blue-500 hover:bg-blue-700 text-sm text-white py-2 rounded mt-3 mb-3 border-box"
            >Open project</button>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import global_mixin from "@/mixins/global";
export default {
  mixins: [global_mixin],
  data() {
    return {
      list_portofolio: [],
      local_env: "prod"
    };
  },
  methods: {
    generate_sample_data: function() {
      for (let index = 0; index < 100; index++) {
        this.list_portofolio.push({
          name: "name" + index
        });
      }
    },
    get_folder_project: async function() {
      try {
        let setting = await this.get_app_setting(this.my_setting);
        let response = await fetch(this.url_home);
        let project = await response.json();
        this.list_portofolio = project.filter(
          item =>
            item.type == "dir" && !setting.excluded_folder.includes(item.name)
        );
      } catch (error) {
        window.alert(error);
      }
    }
  },
  mounted() {
    if (this.local_env == "prod") {
      this.get_folder_project(this.my_setting);
    } else {
      this.generate_sample_data();
    }
  },
  computed: {
    ...mapState({
      url_home: "url_home_portofolio",
      base_url: "base_url",
      vue_logo: "vue_logo",
      my_setting: "my_setting"
    })
  }
};
</script>
